import React, { useState } from 'react'
import { Formik, Field } from 'formik'
import { graphql, useStaticQuery } from 'gatsby'
import {
    optionItemDefault,
    leverageItemLists,
    syntheticItemLists,
    financialItemLists,
} from '../common/_underlying-data'
import {
    BreadCrumbContainer,
    CalculateButton,
    CalculatorBody,
    CalculatorForm,
    CalculatorHeader,
    CalculatorLabel,
    CalculatorOutputContainer,
    CalculatorOutputField,
    CalculatorOutputSymbol,
    CalculatorTabItem,
    ContentContainer,
    FormulaText,
    FormWrapper,
    header_style,
    InputGroup,
    item_style,
    LinkWrapper,
    RightContent,
    SectionSubtitle,
    StyledLinkButton,
    StyledOl,
    StyledSection,
} from '../common/_style'
import validation from '../common/_validation'
import { localize, Localize } from 'components/localization'
import { Flex, Show } from 'components/containers'
import {
    Accordion,
    AccordionItem,
    Dropdown,
    DropdownSearch,
    Header,
    LocalizedLinkText,
    QueryImage,
    Text,
} from 'components/elements'
import Input from 'components/form/input'
import RightArrow from 'images/svg/black-right-arrow.svg'

const MarginCalculator = () => {
    const query = graphql`
        query {
            margin_formula: file(relativePath: { eq: "trade-tools/margin-formula.png" }) {
                ...fadeIn
            }
            margin_info: file(relativePath: { eq: "trade-tools/margin-info.png" }) {
                ...fadeIn
            }
            margin_formula_mobile: file(
                relativePath: { eq: "trade-tools/margin-formula-mobile.png" }
            ) {
                ...fadeIn
            }
        }
    `
    const data = useStaticQuery(query)

    const [tab, setTab] = useState('Synthetic')

    const onTabClick = (tab) => {
        setTab(tab)
    }

    const getMargin = (values) => {
        const { symbol, volume, assetPrice, leverage, contractSize } = values
        let margin_formula
        const STEPINDEX_VALUE = 100
        const RANGEBREAK100VALUE = 400
        const RANGEBREAK200VALUE = 800

        if (symbol.name === 'Step Index') {
            margin_formula = volume * STEPINDEX_VALUE
        } else if (symbol.name === 'Range Break 100 Index') {
            margin_formula = volume * RANGEBREAK100VALUE
        } else if (symbol.name === 'Range Break 200 Index') {
            margin_formula = volume * RANGEBREAK200VALUE
        } else {
            margin_formula = (volume * contractSize * assetPrice) / leverage.name
        }

        return toFixed(margin_formula)
    }

    const toFixed = (val) => {
        return parseFloat(val.toFixed(3)).toLocaleString()
    }

    const resetValidation = (values) => {
        const errors = {}
        const symbol_error = validation.symbol(values.symbol)
        const volume_error = validation.volume(values.volume)
        const assetPrice_error = validation.assetPrice(values.assetPrice)
        const leverage_error = validation.leverage(values.leverage)

        if (symbol_error) {
            errors.symbol = symbol_error
        }
        if (volume_error) {
            errors.volume = volume_error
        }
        if (assetPrice_error) {
            errors.assetPrice = assetPrice_error
        }
        if (leverage_error) {
            errors.leverage = leverage_error
        }

        return errors
    }

    const getMarginCurrency = (symbol) => {
        let currency = 'USD'
        if (symbol.market === 'synthetic_indices' || symbol.market === 'commodities') {
            currency = 'USD'
        }

        if (symbol.name === 'DAX_30') {
            currency = 'EUR'
        }

        if (symbol.market === 'forex' && symbol.name !== 'default' && symbol.name !== 'CL_BRENT') {
            currency = symbol.display_name.slice(-3)
        }

        return currency
    }

    const getContractSize = (symbol) => {
        let contractSize = 1 //crypto falls into this contract size

        if (symbol.market === 'forex') {
            contractSize = 100000
        }

        if (symbol.market === 'commodities') {
            switch (symbol.name) {
                case 'XAGUSD':
                    contractSize = 5000
                    break
                case 'XAUUSD':
                case 'XPDUSD':
                case 'XPTUSD':
                    contractSize = 100
                    break
            }
        }

        if (symbol.name === 'Step Index') {
            contractSize = 10
        }

        if (symbol.market === 'smartfx') {
            contractSize = 100
        }

        return contractSize
    }

    const numberWithCommas = (input) => {
        return input.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
    }

    const numberSubmitFormat = (input) => {
        return input.replace(/^0+(?!\.|$)/, '')
    }

    return (
        <>
            <BreadCrumbContainer>
                <Flex jc="flex-start" ai="center">
                    <LocalizedLinkText to="/trader-tools" color="grey-5">
                        {localize("Traders' tools")}
                    </LocalizedLinkText>
                    <img
                        src={RightArrow}
                        alt={localize('right arrow')}
                        height="16"
                        width="16"
                        style={{ margin: '0 8px' }}
                    />
                    <Text>{localize('Margin calculator')}</Text>
                </Flex>
            </BreadCrumbContainer>
            <StyledSection direction="column">
                <SectionSubtitle as="h3" type="sub-section-title" align="center" weight="normal">
                    {localize(
                        'Our margin calculator helps you to estimate the margin required to keep your positions open overnight on Deriv MetaTrader 5 (DMT5).',
                    )}
                </SectionSubtitle>
                <ContentContainer mt="8rem" mb="4rem">
                    <FormWrapper>
                        <Formik
                            enableReinitialize
                            initialValues={{
                                accountType: 'Synthetic',
                                margin: 0,
                                marginSymbol: 'USD',
                                symbol: '',
                                volume: '',
                                assetPrice: '',
                                leverage: '',
                                optionList: syntheticItemLists,
                                contractSize: '',
                            }}
                            validate={resetValidation}
                            onSubmit={(values, { setFieldValue }) => {
                                setFieldValue('margin', getMargin(values))
                                setFieldValue('volume', numberSubmitFormat(values.volume))
                                setFieldValue('assetPrice', numberSubmitFormat(values.assetPrice))
                            }}
                        >
                            {({
                                values,
                                setFieldValue,
                                setFieldError,
                                setFieldTouched,
                                handleBlur,
                                errors,
                                touched,
                                setErrors,
                                resetForm,
                                isValid,
                                dirty,
                            }) => (
                                <CalculatorForm>
                                    <CalculatorHeader>
                                        <CalculatorLabel htmlFor="message">
                                            {localize('Margin required')}
                                        </CalculatorLabel>
                                        <CalculatorOutputContainer>
                                            <CalculatorOutputField>
                                                {numberWithCommas(values.margin)}
                                            </CalculatorOutputField>
                                            <CalculatorOutputSymbol>
                                                {values.marginSymbol}
                                            </CalculatorOutputSymbol>
                                        </CalculatorOutputContainer>
                                    </CalculatorHeader>
                                    <CalculatorBody>
                                        <CalculatorLabel>
                                            {localize('Account type')}
                                        </CalculatorLabel>
                                        <Flex
                                            mb="3rem"
                                            mt="1rem"
                                            jc="space-between"
                                            tablet={{ height: 'unset' }}
                                        >
                                            <CalculatorTabItem
                                                active={tab === 'Synthetic'}
                                                onClick={() => {
                                                    onTabClick('Synthetic')
                                                    setErrors({})
                                                    resetForm({})
                                                }}
                                            >
                                                <Text align="center">{localize('Synthetic')}</Text>
                                            </CalculatorTabItem>
                                            <CalculatorTabItem
                                                active={tab === 'Financial'}
                                                disabled={tab === 'Financial'}
                                                onClick={() => {
                                                    onTabClick('Financial')
                                                    setErrors({})
                                                    resetForm({})
                                                    setFieldValue('accountType', 'Financial')
                                                    setFieldValue('optionList', financialItemLists)
                                                }}
                                            >
                                                <Text align="center">{localize('Financial')}</Text>
                                            </CalculatorTabItem>
                                        </Flex>
                                        <DropdownSearch
                                            id="symbol"
                                            key={tab}
                                            contract_size={values.contractSize}
                                            default_item={optionItemDefault}
                                            error={touched.symbol && errors.symbol}
                                            items={values.optionList}
                                            label={localize('Symbol')}
                                            onChange={(value) => {
                                                setFieldValue(
                                                    'marginSymbol',
                                                    getMarginCurrency(value),
                                                )
                                                setFieldValue(
                                                    'contractSize',
                                                    getContractSize(value),
                                                )
                                                setFieldValue('symbol', value)
                                            }}
                                            selected_item={values.symbol}
                                            onBlur={handleBlur}
                                        />
                                        <InputGroup>
                                            <Field
                                                name="volume"
                                                value={values.volume}
                                                onChange={(value) => {
                                                    setFieldValue('volume', value)
                                                }}
                                            >
                                                {({ field }) => (
                                                    <Input
                                                        {...field}
                                                        id="volume"
                                                        type="text"
                                                        label={localize('Volume')}
                                                        autoComplete="off"
                                                        error={touched.volume && errors.volume}
                                                        onBlur={handleBlur}
                                                        handleError={(current_input) => {
                                                            setFieldValue('volume', '', false)
                                                            setFieldError('volume', '')
                                                            setFieldTouched('volume', false, false)
                                                            current_input.focus()
                                                        }}
                                                        maxLength="8"
                                                        background="white"
                                                    />
                                                )}
                                            </Field>
                                        </InputGroup>
                                        <InputGroup>
                                            <Field
                                                name="assetPrice"
                                                value={values.assetPrice}
                                                onChange={(value) => {
                                                    setFieldValue('assetPrice', value)
                                                }}
                                            >
                                                {({ field }) => (
                                                    <Input
                                                        {...field}
                                                        id="assetPrice"
                                                        type="text"
                                                        label={localize('Asset price')}
                                                        autoComplete="off"
                                                        error={
                                                            touched.assetPrice && errors.assetPrice
                                                        }
                                                        onBlur={handleBlur}
                                                        handleError={(current_input) => {
                                                            setFieldValue('assetPrice', '', false)
                                                            setFieldError('assetPrice', '')
                                                            setFieldTouched(
                                                                'assetPrice',
                                                                false,
                                                                false,
                                                            )
                                                            current_input.focus()
                                                        }}
                                                        maxLength="15"
                                                        background="white"
                                                    />
                                                )}
                                            </Field>
                                        </InputGroup>
                                        <Dropdown
                                            option_list={leverageItemLists}
                                            id="leverage"
                                            label={localize('Leverage')}
                                            default_option={optionItemDefault}
                                            selected_option={values.leverage}
                                            onChange={(value) => {
                                                setFieldValue('leverage', value)
                                            }}
                                            error={touched.leverage && errors.leverage}
                                            onBlur={handleBlur}
                                            autoComplete="off"
                                            data-lpignore="true"
                                        />
                                        <Flex mt="3rem">
                                            <CalculateButton
                                                secondary
                                                type="submit"
                                                disabled={!isValid || !dirty}
                                            >
                                                {localize('Calculate')}
                                            </CalculateButton>
                                        </Flex>
                                    </CalculatorBody>
                                </CalculatorForm>
                            )}
                        </Formik>
                    </FormWrapper>
                    <RightContent>
                        <Header as="h3" type="section-title" mb="0.8rem">
                            {localize('How to calculate margin')}
                        </Header>
                        <Text>
                            <Localize translate_text="The margin required for a contract on DMT5 is calculated based on the formula:" />
                        </Text>
                        <Text mb="1.6rem">
                            <Localize
                                translate_text="<0>Margin = (volume × contract size × asset price) ÷ leverage</0>"
                                components={[<strong key={0} />]}
                            />
                        </Text>
                        <Text mb="1.6rem">
                            <Localize translate_text="This gives you the margin requirement in the quote currency for forex pairs, or in the denomination of the underlying asset for other instruments." />
                        </Text>
                        <Text mb="40px">
                            {localize(
                                'For instance, if you are trading the USD/CHF forex pair, the margin requirement will be calculated in Swiss Franc (CHF) which is the quote currency. On the other hand, if you are trading Volatility Index 75,  then the margin requirement will be calculated in US Dollar (USD), which is the denomination of the underlying asset – Volatility Index 75.',
                            )}
                        </Text>
                        <Header as="h3" type="section-title" mb="0.8rem">
                            {localize('Example calculation')}
                        </Header>
                        <Accordion has_single_state>
                            <AccordionItem
                                header={localize('Margin required')}
                                header_style={header_style}
                                style={item_style}
                                plus
                            >
                                <Text mb="16px">
                                    {localize(
                                        'Let’s say you want to trade two lots of EUR/USD with an asset price of 1.10 USD and leverage of 100.',
                                    )}
                                </Text>
                                <Show.Desktop>
                                    <QueryImage
                                        data={data.margin_formula}
                                        alt={localize('Margin formula')}
                                    />
                                </Show.Desktop>
                                <Show.Mobile>
                                    <QueryImage
                                        data={data.margin_formula_mobile}
                                        alt={localize('Margin formula mobile')}
                                    />
                                </Show.Mobile>
                                <FormulaText>
                                    <StyledOl>
                                        <li>
                                            <span>
                                                <Localize translate_text="One standard lot of forex = 100,000 units" />
                                            </span>
                                        </li>
                                    </StyledOl>
                                </FormulaText>
                                <Text mb="1.6rem" mt="1.6rem">
                                    <Localize
                                        translate_text="So you will require a margin rate of <0>2,200 USD</0> to open the above position."
                                        components={[<strong key={0} />]}
                                    />
                                </Text>
                                <Text>
                                    {localize(
                                        'Note that these are approximate values only and will differ depending on the leverage that is set for your account and the asset you want to trade.',
                                    )}
                                </Text>
                            </AccordionItem>
                        </Accordion>
                        <LinkWrapper height="auto">
                            <StyledLinkButton
                                tertiary="true"
                                is_deriv_app_link
                                to="/mt5"
                                external="true"
                                target="_blank"
                                rel="noopener noreferrer"
                            >
                                {localize('Go to DMT5 dashboard')}
                            </StyledLinkButton>
                            <StyledLinkButton secondary="true" to="/trade-types/margin">
                                {localize('Learn more about margin')}
                            </StyledLinkButton>
                        </LinkWrapper>
                    </RightContent>
                </ContentContainer>
            </StyledSection>
        </>
    )
}

export default MarginCalculator
